html, body, #root, .App {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: #f4f4f4;  /* Light background for contrast */
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Flipbook container */
.single-page-book {
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.5);
  background-color: #fff;
}

/* Each page */
.page {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Image with anti-aliasing and centering */
.flip-img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 12px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);
  image-rendering: auto;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
}

/* Navigation buttons */
.nav-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
  background: rgba(255, 255, 255, 0.6);
  border: none;
  font-size: 2rem;
  padding: 10px 15px;
  color: black;
  cursor: pointer;
  border-radius: 50%;
}

.prev {
  left: 20px;
}

.next {
  right: 20px;
}
